/// <reference path="typings/tsd.d.ts"/>

import * as React from "react";
import * as Moment from "moment";

import Timer from "./Timer";
import Actions from "./Actions";

import Enumerable from "./Enumerable";
import { Promise } from "es6-promise";
import * as Request from "superagent";

import ClockStore from "./ClockStore";

import * as Extend from "extend"

import * as Typescript from "typescript";

class ImmutableUtils {

    public static makeMutable<T>(obj: T): T {
		return Extend(true, {}, obj);
    }

}

var init = () => {
	Moment.locale("nb");
};

init();

namespace ServiceResponse {

    export interface GeocodedWaypoint {
        geocoder_status: string;
        place_id: string;
        types: string[];
    }

    export interface Bounds {
        northeast: Location;
        southwest: Location;
    }

    export interface Time {
        text: string;
        time_zone: string;
        value: number;
    }

    export interface Distance {
        text: string;
        value: number;
    }

    export interface Duration {
        text: string;
        value: number;
    }

    export interface Polyline {
        points: string;
    }

    export interface Location {
        lat: number;
        lng: number;
    }

    export interface Stop {
        location: Location;
        name: string;
    }

    export interface Agency {
        name: string;
        phone: string;
        url: string;
    }

    export interface Vehicle {
        icon: string;
        name: string;
        type: string;
    }

    export interface Line {
        agencies: Agency[];
        color: string;
        name: string;
        short_name: string;
        text_color: string;
        vehicle: Vehicle;
    }

    export interface TransitDetails {
        arrival_stop: Stop;
        arrival_time: Time;
        departure_stop: Stop;
        departure_time: Time;
        headsign: string;
        line: Line;
        num_stops: number;
    }

    export interface Step {
        distance: Distance;
        duration: Duration;
        end_location: Location;
        html_instructions: string;
        polyline: Polyline;
        start_location: Location;
        steps: Step[];
        travel_mode: string;
        transit_details: TransitDetails;
    }

    export interface Leg {
        arrival_time: Time;
        departure_time: Time;
        distance: Distance;
        duration: Duration;
        end_address: string;
        end_location: Location;
        start_address: string;
        start_location: Location;
        steps: Step[];
        via_waypoint: any[];
    }

    export interface Route {
        bounds: Bounds;
        copyrights: string;
        legs: Leg[];
        overview_polyline: Polyline;
        summary: string;
        warnings: string[];
        waypoint_order: any[];
    }

    export interface Root {
        geocoded_waypoints: GeocodedWaypoint[];
        routes: Route[];
        status: string;
    }

}

interface DirectionsServiceResult {

}

class DirectionsService {

	constructor(private apiKey:string) {}

	private getApiUrl(origin: string, destination: string): string {
		// AIzaSyC5ajALcsSWg85vCH-e07XYJBB0I_En5jI
		return `http://crossorigin.me/https://maps.googleapis.com/maps/api/directions/json?key=${this.apiKey}&origin=${origin}&destination=${destination}&mode=transit&transit_mode=train&alternatives=true`;
	}

	private parseResponse(response: ServiceResponse.Root) {
		var test = Enumerable.fromArray(response.routes).select(r => Enumerable.fromArray(r.legs).first().distance);
		console.log(response);
		return null;
	}

	getDirections(origin: string, destination: string) {
		return new Promise<DirectionsServiceResult>((resolve, reject) => {
			Request
				.get(this.getApiUrl(origin, destination))
				.accept("application/json")
				.end((err, res) => console.log(this.parseResponse(JSON.parse(res.text))));
		});
	}

}



